import React, { Component } from "react";
import SideDrower from "./SideDrower";

class Header extends Component {
  state = {
    visible: false
  };

  sideDrawerOpen = () => {
    this.setState(prev => ({ visible: !prev.visible }));
  };

  togglerControle = () => {
    if (this.state.visible === false) {
      return (
        <div className="lines">
          <span className="line-1" />
          <span className="line-2" />
          <span className="line-3" />
        </div>
      );
    } else {
      return (
        <div>
          <span
            data-balloon="size: 7x"
            data-balloon-pos="up"
            class="db color-inherit link hover-orange"
          >
            <svg
              aria-hidden="true"
              focusable="false"
              data-prefix="fas"
              data-icon="window-close"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 512 512"
              class="svg-inline--fa fa-window-close fa-w-16 fa-7x"
            >
              <path
                fill="currentColor"
                d="M464 32H48C21.5 32 0 53.5 0 80v352c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48V80c0-26.5-21.5-48-48-48zm-83.6 290.5c4.8 4.8 4.8 12.6 0 17.4l-40.5 40.5c-4.8 4.8-12.6 4.8-17.4 0L256 313.3l-66.5 67.1c-4.8 4.8-12.6 4.8-17.4 0l-40.5-40.5c-4.8-4.8-4.8-12.6 0-17.4l67.1-66.5-67.1-66.5c-4.8-4.8-4.8-12.6 0-17.4l40.5-40.5c4.8-4.8 12.6-4.8 17.4 0l66.5 67.1 66.5-67.1c4.8-4.8 12.6-4.8 17.4 0l40.5 40.5c4.8 4.8 4.8 12.6 0 17.4L313.3 256l67.1 66.5z"
                class=""
              />
            </svg>
          </span>
        </div>
      );
    }
  };

  render() {
    return (
      <React.Fragment>
        <div
          className="toggle-button d-flex justify-content-center align-items-center"
          onClick={this.sideDrawerOpen}
        >
          {this.togglerControle()}
        </div>
        <SideDrower visible={this.state.visible} />
      </React.Fragment>
    );
  }
}

export default Header;
